import Link from 'next/link'
import NavStyles from './styles/NavStyles'
import User from './User'
import Signout from './Signout'
import styled from 'styled-components'
import AutoComplete from './AutoComplete'
import Block from './Block'
import { Component } from 'react'

class Nav extends Component {
  constructor(props) {
    super(props)
    this.contentRef = React.createRef()

    this.state = {
      content: '',
    }
  }
  fillContent = () => {
    const value = this.contentRef.current.value
    this.setState({ content: value })
  }
  render() {
    return (
      <User>
        {({ data: { me } }) => (
          <NavStyles content={this.state.content}>
            {me && (
              <>
                <AutoComplete user={me} />
                <Link href="/inbox">
                  <a className="tab">
                    <Block
                      name="Dashboard"
                      ref={this.contentRef}
                      onMouseOver={this.fillContent}
                    />
                  </a>
                </Link>
                <Link href="/feedupload">
                  <a className="tab2">
                    <Block name="Feed Upload" />
                  </a>
                </Link>
                <Link href="/account">
                  <a className="tab3">
                    <Block name="Admin Settings" />
                  </a>
                </Link>
                <Signout />
              </>
            )}
            {!me && (
              <>
                <Link href="/pricing">
                  <a>Pricing</a>
                </Link>
                <Link href="/login">
                  <a>Login</a>
                </Link>
              </>
            )}
          </NavStyles>
        )}
      </User>
    )
  }
}

export default Nav
